<template>
<div>
	<div class="page-header">
		<mt-header fixed title="Reset Password">
		</mt-header>
	</div>

	<div class="page-body">
		<mt-field label="Email" type="email" v-model="email"></mt-field>
		
		<mt-button @click.native="editItemCancel">Cancel</mt-button>
		<mt-button type="primary" @click.native="editItemSubmit">Reset Password</mt-button>
	</div>
</div>
</template>

<script>
import { Indicator, Toast, MessageBox  } from 'mint-ui';
import BizAspnetMemberShip from '../api/BizAspnetMemberShip.js';

export default {
	name: 'ResetPassword',
	data(){
		return {
			email: ''
		}
	},
	methods:{
	    $message(data)
		{
			Toast({message: data.message, duration: 3000});
		},
		$alert(message, title)
		{
			MessageBox.alert(message, title, {confirmButtonText: 'OK'});
		},	
		showNetworkError()
		{
			this.$message({message: 'The Web Server may be too busy, please try again at a later time.', type: 'error'});
		},
		editItemCancel()
		{
			this.$router.push({ name: 'Login' });
		},
		editItemSubmit() 
		{
			if(this.email == '')
			{
				this.$alert('The email can not be empty.','Warning');
				return;
			}

			Indicator.open();
			BizAspnetMemberShip.SendResetPasswordEmail(email).then((res) => {
				Indicator.close();
				if(res.data.OK){	
					this.$alert('<p>Your login password has been sent to your e-mail address of record. </p><p>If you do not receive the e-mail message, please contact your customer support representative.</p>', 'Congratulation');
					this.$router.push({ name: 'Login' });	
				}
				else
				{
					this.$message({message: 'Your password reseting link can not be sent.  Please verify the email address you typed and try it again',type: 'error'});
					console.log(res.data);
				}
			}).catch(err=>{
				Indicator.close();
				this.showNetworkError();
				console.log(err);
			});
		}
	}
}
</script>

<style scoped>
	.page-header{
		margin-bottom: 40px;
	}
</style>
