import axios from 'axios';
import Utils from '../utils';

export default class BizLogin
{
	static ChangePassword(newPassword)
	{
		let token = Utils.getUserToken();
		let para = {
			ParameterNames: 'newPassword',
			Parameters: [newPassword]
		};

		Object.assign(token, para);

		let url =  Utils.getBaseUrl() + 'FacadeService/GetBizLoginResult/ChangePassword?access_token=C250A210-A970-4F02-A513-5757A90EEE5B';
		return axios.post(url, token);
	}

	static GetCountyListForTax(state,zipcode)
	{
		let token = Utils.getUserToken();
		let para = {
			ParameterNames: 'state|zipcode',
			Parameters: [state,zipcode]
		};

		Object.assign(token, para);

		let url =  Utils.getBaseUrl() + 'FacadeService/GetBizLoginResult/GetCountyListForTax?access_token=C250A210-A970-4F02-A513-5757A90EEE5B';
		return axios.post(url, token);
	}

	static GetOrderInfo(OrderNumber)
	{
		let token = Utils.getUserToken();
		let para = {
			ParameterNames: 'OrderNumber',
			Parameters: [OrderNumber]
		};

		Object.assign(token, para);

		let url =  Utils.getBaseUrl() + 'FacadeService/GetBizLoginResult/GetOrderInfo?access_token=C250A210-A970-4F02-A513-5757A90EEE5B';
		return axios.post(url, token);
	}

	static RecoverPassword(userId)
	{
		let token = Utils.getUserToken();
		let para = {
			ParameterNames: 'userId',
			Parameters: [userId]
		};

		Object.assign(token, para);

		let url =  Utils.getBaseUrl() + 'FacadeService/GetBizLoginResult/RecoverPassword?access_token=C250A210-A970-4F02-A513-5757A90EEE5B';
		return axios.post(url, token);
	}

	static Login(userId, passwordHash, ticks, uuid, accessToken)
	{
		let token = Utils.createUserToken(userId, passwordHash, ticks);
		let para = {
			ParameterNames: 'uuid|accessToken',
			Parameters: [uuid,accessToken]
		};

		Object.assign(token, para);

		let url =  Utils.getBaseUrl() + 'FacadeService/GetBizLoginResult/LoginByAjax?access_token=C250A210-A970-4F02-A513-5757A90EEE5B';
		return axios.post(url, token);
	}

	static LoginByWeb(email, password, uuid, accessToken)
	{
		let token = Utils.createUserToken('', '', '');
		let para = {
			ParameterNames: 'email|password|uuid|accessToken',
			Parameters: [email,password,uuid,accessToken]
		};

		Object.assign(token, para);

		let url =  Utils.getBaseUrl() + 'FacadeService/GetBizLoginResult/LoginWebByAjax?access_token=C250A210-A970-4F02-A513-5757A90EEE5B';
		return axios.post(url, token);
	}

}
