<template>
<div>
	<div class="page-header">
		<mt-header fixed title="Register Customer">
		</mt-header>
	</div>

	<div class="page-body">
		<mt-field label="Email" type="email" v-model="editForm.Email"></mt-field>
		<mt-field label="ContactName" type="text" v-model="editForm.ContactName"></mt-field>
		<mt-field label="CompanyName" type="text" v-model="editForm.CompanyName"></mt-field>
		<mt-field label="Address" type="text" v-model="editForm.Address"></mt-field>
		<mt-field label="Address2" type="text" v-model="editForm.Address2"></mt-field>
		<mt-field label="City" type="text" v-model="editForm.City"></mt-field>

		<c-picker field-label="State" :field-value="editForm.State" 
			:picker-list="stateList" picker-value-key="ShortName" @selectedChanged="onStateChanged">
		</c-picker>

		<mt-field label="Zipcode" type="text" v-model="editForm.Zipcode"></mt-field>
		<mt-field label="OfficePhone" type="text" v-model="editForm.OfficePhone"></mt-field>

		<div>
			<p></p>
			<mt-radio title="Want to pay by purchase order?"
				v-model="enablePO"
				:options="[{label: 'Yes', value: '1'}, {label: 'No', value: '0'}]">
			</mt-radio>		
			<p>Notes: Only Current Datawatch customers are permitted to charge via PO. Please call Card & Key at 301-280-4310 for assistance.</p>
		</div>

		<div v-show="enablePO">
			<mt-field label="CustomerID" type="number" v-model="editForm.CustomerID"></mt-field>
			<mt-field label="BillCity" type="text" v-model="editForm.BillCity"></mt-field>

			<c-picker field-label="BillState" :field-value="editForm.BillState" 
				:picker-list="stateList" picker-value-key="ShortName" @selectedChanged="onBillStateChanged">
			</c-picker>

			<mt-field label="BillZipcode" type="text" v-model="editForm.BillZipcode"></mt-field>
		</div>

		<div>
			<mt-button @click.native="editItemCancel">Cancel</mt-button>
			<mt-button type="primary" @click.native="editItemSubmit">Register</mt-button>
		</div>	
	</div>	
</div>
</template>

<script>
import { Indicator, Toast, MessageBox  } from 'mint-ui';
import Utils from '../utils/index.js';
import BizAspnetMemberShip from '../api/BizAspnetMemberShip.js';

import ComboPicker from '../components/ComboPicker.vue';

export default {
	name: 'Register',
	components:{
		'c-picker': ComboPicker
	},
	data(){
		return {
			editForm: {
				Email: '',
				ContactName: '',
				CompanyName: '',
				Address: '',
				Address2: '',
				City: '',
				State: '',
				Zipcode: '',
				OfficePhone: '',
				CustomerID: 0,
				BillCity: '',
				BillState: '',
				BillZipcode: ''
			},
			enablePO: '0',
			stateList: []
		}
	},
	methods:{
		$message(data)
		{
			Toast({message: data.message, duration: 3000});
		},
		$alert(message, title)
		{
			MessageBox.alert(message, title, {confirmButtonText: 'OK'});
		},	
		showNetworkError()
		{
			this.$message({message: 'The Web Server may be too busy, please try again at a later time.', type: 'error'});
		},
		editItemCancel()
		{
			this.$router.push({ name: 'Profile' });
		},
		onStateChanged(obj)
		{
			this.editForm.State = obj.ShortName;
		},
		onBillStateChanged(obj)
		{
			this.editForm.BillState = obj.ShortName;
		},
		checkUserInput()
		{
			if(this.editForm.Email == '')
			{
				this.$alert('The email can not be empty.','Warning');
				return false;
			}

			if(this.editForm.ContactName == '')
			{
				this.$alert('The ContactName can not be empty.','Warning');
				return false;
			}

			if(this.editForm.CompanyName == '')
			{
				this.$alert('The CompanyName can not be empty.','Warning');
				return false;
			}

			if(this.editForm.Address == '')
			{
				this.$alert('The Address can not be empty.','Warning');
				return false;
			}

			if(this.editForm.City == '')
			{
				this.$alert('The City can not be empty.','Warning');
				return false;
			}

			if(this.editForm.State == '')
			{
				this.$alert('The State can not be empty.','Warning');
				return false;
			}

			if(this.editForm.Zipcode == '')
			{
				this.$alert('The Zipcode can not be empty.','Warning');
				return false;
			}

			if(this.enablePO != '0')
			{
				if(this.editForm.CustomerID == '')
				{
					this.$alert('The CustomerID can not be empty.','Warning');
					return false;
				}

				if(this.editForm.BillCity == '')
				{
					this.$alert('The BillCity can not be empty.','Warning');
					return false;
				}

				if(this.editForm.BillState == '')
				{
					this.$alert('The BillState can not be empty.','Warning');
					return false;
				}

				if(this.editForm.BillZipcode == '')
				{
					this.$alert('The BillZipcode can not be empty.','Warning');
					return false;
				}
			}

			return true;
		},
		editItemSubmit() 
		{
			if(!this.checkUserInput())
				return;

			Indicator.open();
			if(this.enablePO == '0')
			{
				this.editForm.BillCity = this.editForm.City;
				this.editForm.BillState = this.editForm.State;
				this.editForm.BillZipcode = this.editForm.Zipcode;
			}
			
			let customer = Object.assign({}, this.editForm);
			let aspnetMemberShip = {};
			aspnetMemberShip.Email = customer.Email;

			if(this.enablePO == '0')
			{
				customer.CustomerID = 0;
				customer.BillContactName =  customer.ContactName;                          
				customer.BillCompanyName =  customer.CompanyName;                          
				customer.BillAddress     =  customer.Address;                         
				customer.BillAddress2    =  customer.Address2;                        
				customer.BillCity        =  customer.City;                         
				customer.BillState       =  customer.State;                         
				customer.BillZipcode     =  customer.Zipcode;                       
				customer.BillPhone       =  customer.OfficePhone;   
				
				BizAspnetMemberShip.RegisterUser(customer, aspnetMemberShip).then((res) => {
					Indicator.close();
					if(!res.data.OK){	
						this.$message({message: res.data.Error, type: 'error'});
						console.log(res.data);
					}
					else
					{
						this.$alert('<p> Your have created an shopping account on DatawatchSystems.com successfully</p><p>The login password has been sent to your e-mail address which you registered. </p><p>If you do not receive the e-mail conformation please contact your customer support representative</p>', 'Congratulation', {
							dangerouslyUseHTMLString: true});

						this.$router.push({name: 'Login'});
					}
				}).catch(err=>{
					Indicator.close();
					this.showNetworkError();
					console.log(err);
				});
			}
			else
			{
				BizAspnetMemberShip.IsValidCustomer(customer.CustomerID, customer.BillCity, customer.BillState, customer.BillZipcode).then((res) => {
					if(!res.data.OK){	
						Indicator.close();
						this.$alert('<p>The customer account information you typed does not match the record in the database.</p><p>If you are not certain your account information, please call Card & Key at 301-280-4310 for assistance.</p>', 'Error');
						console.log(res.data);
					}
					else
					{
						BizAspnetMemberShip.RegisterUser(customer, aspnetMemberShip).then((res) => {
							Indicator.close();
							if(!res.data.OK){	
								this.$message({message: res.data.Error, type: 'error'});
								console.log(res.data);
							}
							else
							{
								this.$alert('<p> Your have created an shopping account on DatawatchSystems.com successfully</p><p>The login password has been sent to your e-mail address which you registered. </p><p>If you do not receive the e-mail conformation please contact your customer support representative</p>', 'Congratulation');
								this.$router.push({name: 'Login'});
							}
						}).catch(err=>{
							Indicator.close();
							this.showNetworkError();
							console.log(err);
						});
					}
				}).catch(err=>{
					Indicator.close();
					this.showNetworkError();
					console.log(err);
				});
			}	
	
		}
	},
	mounted()
	{
		BizAspnetMemberShip.GetAllState().then((res) => {
			this.stateList = res.data;
		}).catch(err=>{
			this.showNetworkError();
			console.log(err);
		});
	}
}
</script>

<style scoped>
	.page-header{
		margin-bottom: 40px;
	}
</style>
