<template>
<div>
	<mt-field :label="fieldLabel" :type="fieldType" v-model="fieldValue" @click.native="pickerVisible=true" readonly="readonly"></mt-field>

	<mt-popup v-model="pickerVisible" position="bottom" style="width:100%;">
		<mt-picker :slots="pickerSlots" :showToolbar="true" :valueKey="pickerValueKey" @change="onChange">
				<div class="picker-toolbar-title">
				<div class="picker-btn-cancel" @click="onChangedCancel">Cancel</div>
				<div class="">{{pickerTitle}}</div>
				<div class="picker-btn-sure" @click="onChangedOK">OK</div>
			</div>
		</mt-picker>
	</mt-popup>
</div>
</template>

<script>
export default {
	name: 'ComboPicker',
	props:{
		fieldLabel:{
			type: String,
			required: true
		},
		fieldType:{
			type: String,
			default: 'text'
		},
		fieldValue:{
			type: String,
			required: true
		},
		pickerTitle:{
			type: String,
			default: '',
		},
		pickerValueKey:{
			type: String,
			required: true
		},
		pickerList:{
			type: Array,
			required: true
		}
	},
	data(){
		return {
			selectedObj: {},
			pickerVisible: false
		}
	},
	computed:{
		pickerSlots()
		{
			let index = 0;
			for(let i = 0; i < this.pickerList.length; i++)
			{
				if(this.pickerList[i][this.pickerValueKey] == this.fieldValue)
				{
					index = i;
				}
			}

			return [{flex: 1, values: this.pickerList, className: 'slot1', textAlign: 'center', defaultIndex: index}];
		}
	},
	methods:{
		onChange(picker, values)
		{
			if(values.length > 0)
			{
				this.selectedObj = values[0];
			}
		},
		onChangedOK()
		{
			if(Object.keys(this.selectedObj).length > 0)
			{
				this.$emit('selectedChanged', this.selectedObj);
			}
			this.pickerVisible = false;
		},
		onChangedCancel()
		{
			this.pickerVisible = false;
		}
	}
}
</script>

<style scoped>
	.picker-toolbar-title {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        height: 40px;
        line-height: 40px;
        font-size: 16px;
    }
    .picker-btn-cancel, .picker-btn-sure {
        color: #FF6600
    }
</style>
