<template>
<div>
	<mt-field label="Email" type="email" v-model="email" :state="email.length > 0 ? 'success' : 'error'"></mt-field>
	<mt-field label="Password" type="password" v-model="password" :state="password.length > 0 ? 'success' : 'error'"></mt-field>
	<mt-switch v-model="rememberMe">Remember me</mt-switch>

	<mt-button type="primary" size="large" @click.native="loginClick">Login</mt-button>
	<mt-button type="default" size="large" @click.native="registerUserClick">Register</mt-button>

	<router-link :to="{name: 'ResetPassword'}">Forgot password?</router-link>
</div>
</template>

<script>
import { Indicator, Toast, MessageBox  } from 'mint-ui';
import BizLogin from '../api/BizLogin.js';
import Utils from '../utils';

export default {
	name: 'Login',
	data() {
		return {
			email: '',
			password: '',
			rememberMe: true
		};
	},
	methods: {
		$message(data)
		{
			Toast({message: data.message, duration: 3000});
		},
		$alert(message, title)
		{
			MessageBox.alert(message, title, {confirmButtonText: 'OK'});
		},		
		registerUserClick()
		{
			this.$router.push({name: 'Register'});
		},
		loginClick() 
		{
			if(this.email == '')
			{
				this.$alert('The email can not be empty.','Warning');
				return;
			}

			if(this.password == '')
			{
				this.$alert('The password can not be empty.','Warning');
				return;
			}

			Indicator.open();

			let email = this.email;
			let password = this.password;
			let uuid = '';
			let accessToken ='FE1CA273-4EF5-4C1F-8F8E-989C53833BA7';
			BizLogin.LoginByWeb(email, password, uuid, accessToken).then(res => {
				Indicator.close();
				if(res.data.OK){
					let user = JSON.parse(res.data.Tag);
					Utils.setUserToken(user.userId, user.password, user.ticks, user.secret, email, user.customerPK, user.discountRate);
					if(this.rememberMe)
					{
						Utils.remeberMe(email, password);
					}
					else
					{
						Utils.clearRemeberMe();
					}
					this.$router.push({ name: 'Dashboard' });
				}
				else
				{
					Toast({message: res.data.Error, duration: 3000});
					console.log(res.data)
				}
			}).catch(err=>{
				Indicator.close();
				Toast({message: 'The Web Server may be too busy, please try again at a later time.', duration: 3000});
				console.log(err);
			});
		}
	},
	created(){
		this.email = localStorage.getItem(Utils.getAppName() + '_userId') || 'chu.laiguo@chekeit.com';
		this.password = localStorage.getItem(Utils.getAppName() + '_password') || '1234';
	}
}

</script>

<style lang="scss" scoped>
</style>
